<template>
    <div>
        <Head title="个人中心"></Head>
        <h2> mine == 个人中心 </h2>
        <div v-if="isLogin">
            <h2>个人信息...</h2>
            <h2>username == {{username}}  /  {{userInfo.username}}</h2>
            <h2>token == {{userInfo.token }}</h2>
            <Uploadavatar :username="username" />

        </div>
        <div v-else>
            <van-button @click="gotoLogin" type="primary">立即登录</van-button>
        </div>
    </div>
</template>

<script>
import Uploadavatar from "@/components/Uploadavatar.vue"
import {mapState} from "vuex"
export default {
    data(){
        return {
            isLogin:false,
            username:""
        }
    },
    components:{
        Uploadavatar
    },
    computed:{
        ...mapState(['userInfo'])
    },
    methods:{
        gotoLogin(){
            this.$router.push({name:'login'})
        }
    },
    mounted(){
        if(sessionStorage.userInfo){
            this.isLogin = JSON.parse(sessionStorage.userInfo).token;
            this.username = JSON.parse(sessionStorage.userInfo).username;
        }  
        
        this.$axios.get("/vue/mock/data").then(res=>{
            
        })
    }

}
</script>


